module.exports = {"data":{"contentfulRecipe":{"title":"Pork Carinitas","ingredients":{"ingredients":"\n1. 1kg [pork shoulder](https://en.wikipedia.org/wiki/Boston_butt \"pork shoulder\") (this cost max €20)\n2. Olive oil\n3. Bay leaves (4-5)\n4. Garlic Cloves (5-6)\n4. Cinnamon\n5. Cayenne\n6. Italian spice (or thyme, oregano)\n7. Salt\n8. Black Pepper\n9. Juice of 1 orange\n10. Heavy duty tin foil\n11. Deep roasting pan (about 1.5\" deep)\n"},"instructions":{"instructions":"Cut the pork up into 1-2 inch thick pieces (just make sure they are all the same). Then put in olive oil (enough to coat) and salt and pepper. Add in the other spices as well now(about 1 tbl spoon each), and the whole gloves of garlic (unpeeled). Add in the juice from one orange and mix it all around so that each piece of meat is covered. Spread everything around so it's evenly dispersed, you don't want all the garlic or bay leaves in one place. \n\nPlace in the oven at 135C for 3 1/2 hours, then it should be fork tender when you take it out. A nice extra step is to take off the tin foil and place the meat under the broiler set to high to get it crispy. "}}},"pathContext":{"id":"c1ebQaqdNVYcgEwaWIIY4Oa"}}

